.wadi_addons_elements_control_container {
  padding: 30px;
  margin-left: -20px;
}
.wadi_addons_elements_control_container .wadi_elements_info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  background: #fafafa;
  padding: 40px;
  border-radius: 25px;
  align-items: center;
}
.wadi_addons_elements_control_container .wadi_elements_info .wadi_section_info {
  font-size: 1.1rem;
  margin-right: 10px;
}
.wadi_addons_elements_control_container .wadi_elements_info .wadi_section_info h4 {
  font-size: 1.3rem;
}
.wadi_addons_elements_control_container .wadi_elements_info .wadi_section_info p {
  font-size: 1rem;
}
.wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status {
  display: flex;
}
.wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status .wadi_button {
  background-color: #ddd;
  color: white;
  font-size: 1.1rem;
  height: 60px;
  margin: -2px;
  border: 1px solid #ccc;
  cursor: pointer;
  font-weight: 600;
  width: 210px;
}
.wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status .wadi_button.wadi_button-disable {
  border-radius: 0 30px 30px 0;
  padding: 0 31px;
}
.wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status .wadi_button.wadi_button-disable.active {
  background: #898989;
  box-shadow: inset 3.564px 1.816px 7px 0px rgba(81, 81, 81, 0.5);
}
.wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status .wadi_button.wadi_button-enable {
  border-radius: 30px 0 0 30px;
  padding: 0 31px;
}
.wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status .wadi_button.wadi_button-enable.active {
  background: #00c9a9;
  box-shadow: inset 3.564px 1.816px 7px 0px rgba(81, 81, 81, 0.5);
}
@media only screen and (max-width: 1200px) {
  .wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status {
    display: flex;
  }
  .wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status .wadi_button {
    width: 160px;
  }
  .wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status .wadi_button.wadi_button-disable {
    border-radius: 0;
  }
  .wadi_addons_elements_control_container .wadi_elements_info .wadi_elements_status .wadi_button.wadi_button-enable {
    border-radius: 0;
  }
}
@media only screen and (max-width: 768px) {
  .wadi_addons_elements_control_container .wadi_elements_info {
    display: flex;
    flex-direction: column;
  }
  .wadi_addons_elements_control_container .wadi_elements_status {
    display: flex;
  }
  .wadi_addons_elements_control_container .wadi_elements_status .wadi_button {
    font-size: 0.9rem;
  }
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1rem;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f4f4f4;
  padding: 22px;
  border-radius: 20px;
  border: 1px solid #ccc;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .wadi_element_link {
  text-decoration: none;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .wadi_element_link .wadi_element_link_separator {
  width: 3px;
  height: 3px;
  display: inline-block;
  text-align: center;
  color: #000000;
  background: #000000;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  vertical-align: middle;
  margin: 0 3px;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .switch {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 24px;
  -webkit-border-radius: 12px;
  border-radius: 12px;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .switch input {
  display: none;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .wadi_addon_switcher,
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .pro_wadi_addon_switcher {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* background-color: #f2f2f2;
                    box-shadow: 0 0 2px 1px #ccc inset; */
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .wadi_addon_switcher:before,
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .pro_wadi_addon_switcher:before {
  position: absolute;
  content: "";
  left: 3px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  /* background: radial-gradient(farthest-corner at 14px 0px, #f0f0f0 50%, #cacaca 100%); */
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  padding: 0;
  margin: 0;
  width: 15px;
  height: 15px;
  -webkit-border-radius: 8px;
  border-radius: 8px;
  -webkit-filter: drop-shadow(0px 5px 2.5px rgba(40, 40, 40, 0.35));
  filter: drop-shadow(0px 5px 2.5px rgba(40, 40, 40, 0.35));
  background-color: #f7f7f7;
  border: 1px solid #ffffff;
  /* box-shadow: 0 2.5px 5px #b8b8b9; */
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item input:checked + .wadi_addon_switcher {
  background: #00c9a9;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item input:focus + .wadi_addon_switcher {
  -webkit-box-shadow: 0 0 1px #18a4fd;
  box-shadow: 0 0 1px #18a4fd;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item input:checked + .wadi_addon_switcher:before {
  -webkit-transform: translate3d(20px, -50%, 0);
  transform: translate3d(20px, -50%, 0);
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .wadi_addon_switcher.round,
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .pro_wadi_addon_switcher.round {
  -webkit-border-radius: 34px;
  border-radius: 34px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#cfcfcf), to(#dbdbdb));
  background-image: -webkit-linear-gradient(top, #cfcfcf 0%, #dbdbdb 100%);
  background-image: linear-gradient(180deg, #cfcfcf 0%, #dbdbdb 100%);
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .slider.round:before,
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .pro_wadi_addon_switcher.round:before {
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .switch input:disabled + .pro_wadi_addon_switcher::after {
  position: absolute;
  font-family: "Dashicons";
  content: "\f160";
  color: #474747;
  right: 8px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .wadi_element_name {
  font-size: 1.1rem;
  font-weight: 600;
  height: 60px;
  display: flex;
  align-items: center;
}
.wadi_addons_elements_control_container .wadi-elements .wadi-switchers .wadi_element_item .wadi_element_name .wadi_addon_pro_element {
  background: #ffcd1f;
  font-size: 0.8rem;
  color: #fff;
  padding: 6px;
  border-radius: 6px;
  margin-left: 6px;
}

.wadi_swal_modal.swal2-modal {
  background-color: #ffffff;
  opacity: 0.95;
  background-image: repeating-radial-gradient(circle at 0 0, transparent 0, #ffffff 10px), repeating-linear-gradient(#f8f8f855, #f8f8f8);
}
.wadi_swal_modal.swal2-popup {
  border-radius: 30px 0;
}
.wadi_swal_modal .swal2-confirm.swal2-styled {
  background-color: #818181;
  border-radius: 0;
}
.wadi_swal_modal .swal2-cancel.swal2-styled {
  background-color: #00a188;
  border-radius: 0;
}